<script>
  import { onDestroy } from "svelte";
  import Waiter from "../../shared/Waiter.svelte";
  import Icon from "../../shared/Icon.svelte";
  import {
    generateWordpressPostContent,
    saveWordpressPost,
    uploadImageToWordpress,
    notify,
  } from "../../utils";
  import { wordpressUrl } from "../../constants";
  import { addedMedia } from "../../stores";

  let { title, embed } = (() => {
    try {
      return JSON.parse(window.localStorage.getItem("wpConfig")) || {};
    } catch (_) {
      return {};
    }
  })();

  let wordpressRequest,
    wordpressPostId,
    mediaItems,
    canPost,
    wordpressUploadPromise;
  onDestroy(addedMedia.subscribe((items) => (mediaItems = items)));

  const handleSubmit = async () => {
    canPost = true;
    wordpressUploadPromise = Promise.all(
      mediaItems.map(async (mediaItem) => {
        try {
          if (!mediaItem.wordpress) {
            mediaItem.media_url = await uploadImageToWordpress(mediaItem);
            mediaItem.wordpress = true;
            mediaItem.error = null;
          }
        } catch (err) {
          mediaItem.error = err.message;
          canPost = false;
        }
      })
    );
    await wordpressUploadPromise;

    addedMedia.set(mediaItems);

    if (canPost) {
      wordpressRequest = generateWordpressPostContent(mediaItems, embed)
        .then((content) => saveWordpressPost({ title, content }))
        .then((res) => {
          wordpressPostId = res;
          window.localStorage.setItem(
            "wpConfig",
            JSON.stringify({ title: res, embed })
          );
        });
    } else {
      notify("Error uploading to wordpress", "error");
    }
  };
</script>

<style>
  label {
    margin-bottom: 0.5rem;
  }
  input:not([type="checkbox"]) {
    width: 100%;
    margin: 0.3rem 0;
  }
  .actions {
    margin-top: 1rem;
    display: flex;
    justify-content: flex-end;
  }
  .actions button {
    margin-left: 0.3rem;
    flex-grow: 1;
  }
  .success-message {
    text-align: center;
  }
  .success-message span {
    color: #060;
    font-size: 1.3em;
  }
</style>

{#await wordpressUploadPromise}
  <Waiter />
{/await}
{#await wordpressRequest}
  <Waiter />
{/await}
{#if wordpressPostId}
  <div class="success-message">
    <span>Wordpress post saved successfully</span><br />
    <a
      href="{wordpressUrl}/wp-admin/post.php?post={wordpressPostId}&action=edit"
      target="_blank">Click here</a>
    to edit
  </div>
{:else}
  <form on:submit|preventDefault={handleSubmit}>
    <label> Site <input disabled type="url" value={wordpressUrl} /> </label>
    <label>
      Title(New Post) / Post ID (Existing Post)<br />
      <input bind:value={title} required />
    </label>
    <label>
      <input bind:checked={embed} type="checkbox" />
      Use Embedded Instagram Posts
      {#if embed}
        <div>
          <Icon icon="info-circle" size=".85em" color="darkcyan">
            <span slot="text-right">Non-Instagram posts will be ignored in
              embedded mode</span>
          </Icon>
        </div>
      {/if}
    </label>
    <div class="actions"><button type="submit">Submit</button></div>
  </form>
{/if}
